@import url("https://fonts.googleapis.com/css2?family=B612:ital,wght@0,400;0,700;1,400&display=swap");

$mainColor: #326647;
$hoverMainColor: #479164;
$goldColor: #d4af37;
$goldHoverColor: #f5d362;
$navyColor: #000080;

$h1FontSize: calc(32px + 1vw);
$h2FontSize: calc(24px + 0.8vw);
$h3FontSize: calc(18px + 0.7vw);
$h4FontSize: calc(16px + 0.4vw);
$h5FontSize: calc(13px + 0.3vw);
$h6FontSize: calc(10px + 0.3vw);
$normalFontSize: calc(16px + 0.1vw);

* {
  margin: 0;
  padding: 0;
  font-family: "B612", sans-serif;
}

body {
  background: whitesmoke;
}
