@import "variables.scss";

.customersContainer {
  width: 80vw;
  padding-top: 80px;
  display: grid;
  place-items: center;
  padding: 10rem 10vw;
  background: whitesmoke;

  h3 {
    font-size: $h3FontSize;
    text-align: center;
  }

  .slider {
    margin-top: 4rem;
    width: 80%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    place-items: center;

    img {
      width: 80%;
    }

    .arrow {
      color: black;
      &:hover {
        cursor: pointer;
        color: rgb(49, 49, 49);
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .customersContainer {
    .slider {
      width: 100%;
    }
  }
}
