@import "variables.scss";

nav {
  width: 100%;
  height: 80px;
  background: whitesmoke;
  position: fixed;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  font-size: $normalFontSize;
  z-index: 10;

  ul {
    list-style-type: none;
    width: 60%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    //margin-left: auto;

    li {
      a {
        color: rgb(66, 66, 66);
        text-decoration: none;

        &:hover {
          font-weight: bold;
        }
      }
    }
    li + li {
      //margin-
    }
  }

  .menuIcon {
    display: none;
  }

  .contactBtn {
    color: white;
    text-decoration: none;
    background-color: $mainColor;
    padding: 0.75rem 1.5rem;
    border-radius: 0.5rem;

    &:hover {
      background-color: $hoverMainColor;
    }
  }
}

@media screen and (max-width: 960px) {
  nav {
    ul {
      display: none;
    }

    .showUl {
      display: flex;
      flex-direction: column;
      place-items: center;
      background: rgba(0, 0, 0, 0.7);
      width: 100vw;
      height: 100vh;
      position: absolute;
      top: 0;

      padding-top: 120px;
      li {
        //background: yellowgreen;
        width: 100%;
        display: grid;
        place-items: center;
        height: 6rem;
        border-top: 1px solid rgb(110, 110, 110);
        border-bottom: 1px solid rgb(110, 110, 110);
        a {
          color: white;
        }
      }
      li + li {
        border-top: none;
      }
    }

    .menuIcon {
      display: block;
      width: $h1FontSize;
      text-align: center;
      z-index: 15;
    }
  }
}

@media screen and (max-width: 560px) {
  nav {
    .contactBtn {
      padding: 0.75rem 0.25rem;
    }
  }
}
