@import "variables.scss";

.footerContainer {
  width: 80vw;
  //padding-top: 80px;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(3, 1fr);
  padding: 5rem 10vw;
  font-size: $normalFontSize;

  h4 {
    font-size: $h4FontSize;
  }

  p {
    margin-top: 1rem;
  }
}

@media screen and (max-width: 970px) {
  .footerContainer {
    grid-template-columns: repeat(1, 1fr);

    div {
      width: 90%;
    }

    div + div {
      margin-top: 2rem;
    }
  }
}
