@import "variables.scss";

.aboutContainer {
  width: 80vw;
  height: calc(100vh - 80px);
  min-height: 750px;
  padding-top: 80px;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 10vw;

  .aboutLeftSide {
    .teamSvg {
      width: 70%;
      height: auto;
      border-radius: 1rem;
      //background: $mainColor;
      padding: 2rem;
    }
  }

  .aboutRightSide {
    h3 {
      font-size: $h3FontSize;
    }
    p {
      font-size: $normalFontSize;
      text-align: justify;
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 960px) {
  .aboutContainer {
    grid-template-columns: repeat(1, 1fr);
    .aboutLeftSide {
      //width: 100%;
      text-align: center;
      .teamSvg {
        width: 40%;
        height: auto;
      }
    }
    .aboutRightSide {
      h3 {
        text-align: center;
      }
    }
  }
}
