@import "variables.scss";

.contactContainer {
  width: 100vw;
  //padding-top: 80px;
  display: grid;
  place-items: center;
  padding-bottom: 5rem;

  .contactBox {
    width: 40%;
    padding: 5rem 10vw;
    display: flex;
    flex-direction: column;
    border-radius: 1rem;
    background: $mainColor;

    h2 {
      font-size: $h2FontSize;
    }

    h3 {
      font-size: $h3FontSize;
    }

    p {
      font-size: $normalFontSize;
      margin-top: 2rem;
    }

    form {
      margin-top: 2rem;
      display: flex;
      flex-direction: row;
      input {
        font-size: $normalFontSize;
        padding: 1rem;
        border-radius: 1rem;
        outline: none;
        width: 50%;
      }
      button {
        font-size: $normalFontSize;
        color: black;
        text-decoration: none;
        background-color: $goldColor;
        padding: 0.75rem 1.5rem;
        border-radius: 0.5rem;
        outline: none;
        border: none;
        margin-left: 2rem;

        &:hover {
          background-color: $goldHoverColor;
          cursor: pointer;
        }
      }
    }
  }
}

@media screen and (max-width: 970px) {
  .contactContainer {
    .contactBox {
      text-align: center;
      width: 60%;
      form {
        flex-direction: column;
        place-items: center;

        input {
          width: 80%;
        }

        button {
          margin-left: 0;
          margin-top: 1rem;
        }
      }
    }
  }
}
