@import "variables.scss";

.startContainer {
  background: $mainColor;
  width: 80vw;
  height: 100vh;
  min-height: 750px;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 10vw;
  position: relative;

  .startLeftSide {
    height: 40%;
    display: grid;
    align-items: center;

    h1 {
      font-size: $h1FontSize;
      color: rgb(14, 14, 14);
    }
    p {
      font-size: $normalFontSize;
      color: rgb(14, 14, 14);
    }
    a {
      color: rgb(14, 14, 14);
      text-decoration: none;
      background-color: $goldColor;
      font-size: $normalFontSize;
      padding: 0.75rem 1.5rem;
      max-width: 20%;
      min-width: 120px;
      border-radius: 0.5rem;
      text-align: center;
      display: flex;
      flex-direction: row;
      place-items: center;
      z-index: 2;

      &:hover {
        background-color: $goldHoverColor;
      }

      .arrow {
        margin-left: auto;
      }
    }
  }

  .startRightSide {
    text-align: right;
    svg {
      width: 50%;
    }
  }

  .waveTop {
    width: 100%;
    height: auto;
    min-height: 50%;
    position: absolute;
    top: 0;
  }

  .waveBottom {
    width: 100%;
    height: auto;
    min-height: 50%;
    position: absolute;
    bottom: 0;
  }
}

@media screen and (max-width: 960px) {
  .startContainer {
    grid-template-columns: repeat(1, 1fr);
    .startLeftSide {
      place-items: center;
    }
    .startRightSide {
      display: none;
    }
  }
}
