*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}nav{width:100%;height:80px;background:whitesmoke;position:fixed;display:grid;grid-template-columns:repeat(3, 1fr);place-items:center;font-size:calc(16px + 0.1vw);z-index:10}nav ul{list-style-type:none;width:60%;display:grid;grid-template-columns:repeat(3, 1fr)}nav ul li a{color:#424242;text-decoration:none}nav ul li a:hover{font-weight:bold}nav .menuIcon{display:none}nav .contactBtn{color:white;text-decoration:none;background-color:#326647;padding:0.75rem 1.5rem;border-radius:0.5rem}nav .contactBtn:hover{background-color:#479164}@media screen and (max-width: 960px){nav ul{display:none}nav .showUl{display:flex;flex-direction:column;place-items:center;background:rgba(0,0,0,0.7);width:100vw;height:100vh;position:absolute;top:0;padding-top:120px}nav .showUl li{width:100%;display:grid;place-items:center;height:6rem;border-top:1px solid #6e6e6e;border-bottom:1px solid #6e6e6e}nav .showUl li a{color:white}nav .showUl li+li{border-top:none}nav .menuIcon{display:block;width:calc(32px + 1vw);text-align:center;z-index:15}}@media screen and (max-width: 560px){nav .contactBtn{padding:0.75rem 0.25rem}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.brandImage{color:black;text-decoration:none}.brandImage span{background:#d4af37;border-radius:50%;padding:0.5rem}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.startContainer{background:#326647;width:80vw;height:100vh;min-height:750px;display:grid;place-items:center;grid-template-columns:repeat(2, 1fr);padding:0 10vw;position:relative}.startContainer .startLeftSide{height:40%;display:grid;align-items:center}.startContainer .startLeftSide h1{font-size:calc(32px + 1vw);color:#0e0e0e}.startContainer .startLeftSide p{font-size:calc(16px + 0.1vw);color:#0e0e0e}.startContainer .startLeftSide a{color:#0e0e0e;text-decoration:none;background-color:#d4af37;font-size:calc(16px + 0.1vw);padding:0.75rem 1.5rem;max-width:20%;min-width:120px;border-radius:0.5rem;text-align:center;display:flex;flex-direction:row;place-items:center;z-index:2}.startContainer .startLeftSide a:hover{background-color:#f5d362}.startContainer .startLeftSide a .arrow{margin-left:auto}.startContainer .startRightSide{text-align:right}.startContainer .startRightSide svg{width:50%}.startContainer .waveTop{width:100%;height:auto;min-height:50%;position:absolute;top:0}.startContainer .waveBottom{width:100%;height:auto;min-height:50%;position:absolute;bottom:0}@media screen and (max-width: 960px){.startContainer{grid-template-columns:repeat(1, 1fr)}.startContainer .startLeftSide{place-items:center}.startContainer .startRightSide{display:none}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.customersContainer{width:80vw;padding-top:80px;display:grid;place-items:center;padding:10rem 10vw;background:whitesmoke}.customersContainer h3{font-size:calc(18px + 0.7vw);text-align:center}.customersContainer .slider{margin-top:4rem;width:80%;display:grid;grid-template-columns:repeat(6, 1fr);place-items:center}.customersContainer .slider img{width:80%}.customersContainer .slider .arrow{color:black}.customersContainer .slider .arrow:hover{cursor:pointer;color:#313131}@media screen and (max-width: 960px){.customersContainer .slider{width:100%}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.offerContainer{background:#326647;width:80vw;height:calc(100vh - 80px);min-height:750px;padding-top:80px;display:grid;place-items:center;padding:0 10vw;-webkit-clip-path:polygon(0% 0%, 100% 20%, 100% 100%, 0% 100%);clip-path:polygon(0% 0%, 100% 20%, 100% 100%, 0% 100%)}.offerContainer .offerHead{display:flex;flex-direction:column;place-items:center;margin-top:4rem}.offerContainer .offerHead h2{font-size:calc(24px + 0.8vw)}.offerContainer .offerHead p{font-size:calc(16px + 0.4vw);margin-top:2rem;width:70%;text-align:center}.offerContainer .windows{display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:6vh;margin-bottom:6rem}.offerContainer .windows .window{background:whitesmoke;margin:1rem;padding:1rem 2rem;height:100%;border-radius:0.5rem;display:grid;flex-direction:column}.offerContainer .windows .window .head{display:flex;flex-direction:row;place-items:center}.offerContainer .windows .window .head .icon{font-size:calc(18px + 0.7vw)}.offerContainer .windows .window .head h4{font-size:calc(16px + 0.4vw);margin-left:2rem}.offerContainer .windows .window p{font-size:calc(16px + 0.1vw);text-align:justify}@media screen and (max-width: 960px){.offerContainer{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;-webkit-clip-path:polygon(0% 0%, 100% 4%, 100% 100%, 0% 100%);clip-path:polygon(0% 0%, 100% 4%, 100% 100%, 0% 100%)}.offerContainer .windows{grid-template-columns:repeat(1, 1fr);grid-row-gap:5rem;width:90%}}@media screen and (max-width: 560px){.offerContainer{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.offerContainer .windows{grid-template-columns:repeat(1, 1fr);grid-row-gap:5rem;width:100%}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.aboutContainer{width:80vw;height:calc(100vh - 80px);min-height:750px;padding-top:80px;display:grid;place-items:center;grid-template-columns:repeat(2, 1fr);padding:0 10vw}.aboutContainer .aboutLeftSide .teamSvg{width:70%;height:auto;border-radius:1rem;padding:2rem}.aboutContainer .aboutRightSide h3{font-size:calc(18px + 0.7vw)}.aboutContainer .aboutRightSide p{font-size:calc(16px + 0.1vw);text-align:justify;margin-top:2rem}@media screen and (max-width: 960px){.aboutContainer{grid-template-columns:repeat(1, 1fr)}.aboutContainer .aboutLeftSide{text-align:center}.aboutContainer .aboutLeftSide .teamSvg{width:40%;height:auto}.aboutContainer .aboutRightSide h3{text-align:center}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.contactContainer{width:100vw;display:grid;place-items:center;padding-bottom:5rem}.contactContainer .contactBox{width:40%;padding:5rem 10vw;display:flex;flex-direction:column;border-radius:1rem;background:#326647}.contactContainer .contactBox h2{font-size:calc(24px + 0.8vw)}.contactContainer .contactBox h3{font-size:calc(18px + 0.7vw)}.contactContainer .contactBox p{font-size:calc(16px + 0.1vw);margin-top:2rem}.contactContainer .contactBox form{margin-top:2rem;display:flex;flex-direction:row}.contactContainer .contactBox form input{font-size:calc(16px + 0.1vw);padding:1rem;border-radius:1rem;outline:none;width:50%}.contactContainer .contactBox form button{font-size:calc(16px + 0.1vw);color:black;text-decoration:none;background-color:#d4af37;padding:0.75rem 1.5rem;border-radius:0.5rem;outline:none;border:none;margin-left:2rem}.contactContainer .contactBox form button:hover{background-color:#f5d362;cursor:pointer}@media screen and (max-width: 970px){.contactContainer .contactBox{text-align:center;width:60%}.contactContainer .contactBox form{flex-direction:column;place-items:center}.contactContainer .contactBox form input{width:80%}.contactContainer .contactBox form button{margin-left:0;margin-top:1rem}}

*{margin:0;padding:0;font-family:"B612", sans-serif}body{background:whitesmoke}.footerContainer{width:80vw;display:grid;place-items:center;grid-template-columns:repeat(3, 1fr);padding:5rem 10vw;font-size:calc(16px + 0.1vw)}.footerContainer h4{font-size:calc(16px + 0.4vw)}.footerContainer p{margin-top:1rem}@media screen and (max-width: 970px){.footerContainer{grid-template-columns:repeat(1, 1fr)}.footerContainer div{width:90%}.footerContainer div+div{margin-top:2rem}}

