@import "variables.scss";

.offerContainer {
  background: $mainColor;
  width: 80vw;
  height: calc(100vh - 80px);
  min-height: 750px;
  padding-top: 80px;
  display: grid;
  place-items: center;
  padding: 0 10vw;
  clip-path: polygon(0% 0%, 100% 20%, 100% 100%, 0% 100%);

  .offerHead {
    display: flex;
    flex-direction: column;
    place-items: center;
    margin-top: 4rem;

    h2 {
      font-size: $h2FontSize;
    }
    p {
      font-size: $h4FontSize;
      margin-top: 2rem;
      width: 70%;
      text-align: center;
    }
  }

  .windows {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 6vh;
    margin-bottom: 6rem;

    .window {
      background: whitesmoke;
      margin: 1rem;
      padding: 1rem 2rem;
      height: 100%;
      border-radius: 0.5rem;
      display: grid;
      flex-direction: column;

      .head {
        display: flex;
        flex-direction: row;
        place-items: center;

        .icon {
          font-size: $h3FontSize;
        }
        h4 {
          font-size: $h4FontSize;
          margin-left: 2rem;
        }
      }
      p {
        font-size: $normalFontSize;
        text-align: justify;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .offerContainer {
    height: fit-content;
    clip-path: polygon(0% 0%, 100% 4%, 100% 100%, 0% 100%);
    .windows {
      grid-template-columns: repeat(1, 1fr);
      grid-row-gap: 5rem;
      width: 90%;
      //margin-top: 4rem;
    }
  }
}

@media screen and (max-width: 560px) {
  .offerContainer {
    height: fit-content;

    .windows {
      grid-template-columns: repeat(1, 1fr);
      grid-row-gap: 5rem;
      width: 100%;
      //margin-top: 4rem;
    }
  }
}
